#landingPage {
  height: 100vh;
  text-align: center;
  background: -webkit-gradient(linear, 
  left top, left bottom, 
  from(rgba(252, 75, 26, 0.475)), 
  to(rgba(168, 252, 26, 0.475))), 
  url(../../images/tree_background.png);
  background: -webkit-linear-gradient(rgba(252, 75, 26, 0.475), 
  rgba(168, 252, 26, 0.475)), 
  url(../../images/tree_background.png);
  background: -o-linear-gradient(rgba(252, 75, 26, 0.475), 
  rgba(168, 252, 26, 0.475)), 
  url(../../images/tree_background.png);
  background: linear-gradient(rgba(252, 75, 26, 0.475), 
  rgba(168, 252, 26, 0.475)), 
  url(../../images/tree_background.png);
  background-size: cover;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: space-evenly;
      justify-content: space-evenly;
}



