#aboutPage {
  max-width: 800px;
  margin: 20px auto;
}

#aboutPage p {
  padding: 10px;
}

#aboutText a {
  text-decoration: none;
  color: rgb(160, 43, 11);
  -webkit-transition: ease-out 0.1s;
  -o-transition: ease-out 0.1s;
  transition: ease-out 0.1s;
}

#aboutText a:hover {
  text-decoration: underline;
}


