#navbar {
  position: fixed;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: end;
      justify-content: flex-end;
  width: 100%;
  -ms-flex-line-pack: center;
      align-content: center;
  background: -webkit-gradient(linear, left top, right top, from(#00000000), to(#ffffff79));
  background: -webkit-linear-gradient(left, #00000000, #ffffff79);
  background: -o-linear-gradient(left, #00000000, #ffffff79);
  background: linear-gradient(to right, #00000000, #ffffff79);
  margin: 0 auto;
  z-index: 11;
  height: 61px; 
}

#navbar a {
  text-decoration: none;
  color: black;
  padding: 20px;
  -webkit-transition: ease-out 0.1s;
  -o-transition: ease-out 0.1s;
  transition: ease-out 0.1s;
}

#navbar a:hover, #havbar a:active {
  background-color: #ffffff79;
}

@media only screen and (max-width: 550px) {
  #navbar {
    display: grid;
    grid-template: auto auto / auto auto;
  }

  #navbar a {
    padding: 3px;

  }
}




